<template>
  <div>
    <div style="margin: 10px 0">
     <el-upload action="http://localhost:8088/file/upload" :show-file-list="false" :on-success="handleFileUpSuccess" style="display: inline-block">
        <el-button type="primary" class="ml-5">上传文件 <i class="el-icon-bottom"></i></el-button>
      </el-upload>
      <el-popconfirm
          class="ml-5"
          confirm-button-text='确定'
          cancel-button-text='我再想想'
          icon="el-icon-info"
          icon-color="red"
          title="您确定批量删除这些数据吗？"
          @confirm="delBatch"
      >
        <el-button type="danger" slot="reference">批量删除 <i class="el-icon-remove-outline"></i></el-button>
      </el-popconfirm>
    </div>
    <el-table
      :data="tableData"
      border
      stripe
      :header-cell-class-name="'headerBg'"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column
        prop="name"
        label="文件名称"
        width="140"
      ></el-table-column>
      <el-table-column
        prop="type"
        label="文件类型(KB)"
        width="120"
      ></el-table-column>
      <el-table-column prop="size" label="文件大小"></el-table-column>
      <el-table-column label="下载">
           <template slot-scope="scope">
            <el-button type="primary" @click="window.open(scope.row.url)"> 
                
            </el-button>
        </template>
      </el-table-column>
      <el-table-column label="启用">
        <template slot-scope="scope">
            <el-swicth v-model="scope.row.enable" active-color= "#13ce66" inactive-color="#ccc"> 
                
            </el-swicth>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="200" align="center">
        <template slot-scope="scope">
          <el-popconfirm
            class="ml-5"
            confirm-button-text="确定"
            cancel-button-text="我再想想"
            icon="el-icon-info"
            icon-color="red"
            title="您确定删除吗？"
            @confirm="del(scope.row.stuNum)"
          >
            <el-button type="danger" slot="reference"
              >删除 <i class="el-icon-remove-outline"></i
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "Files",
  data() {
    return {
      tableData: [],
      name: "",
      multipleSelection:[],

    };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      this.request
        .get("/file/page", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            nickname: this.nickname,
            stuNum: this.stuNum,
            pwd: this.pwd,
            phone: this.phone,
            grade: this.grade,
            avatar: this.avatar,
          },
        })
        .then((res) => {
          console.log(res);
          // console.log("hhhh");
          this.tableData = res.records;
          this.total = res.total;
        });
    },
    del(stuNum) {
      this.request.delete("/file/" + stuNum).then((res) => {
        if (res) {
          this.$message.success("删除成功");
          this.load();
        } else {
          this.$message.error("删除失败");
        }
      });
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    delBatch() {
      let stuNums = this.multipleSelection.map((v) => v.stuNum); // [{}, {}, {}] => [1,2,3]
      this.request.post("/file/batch", stuNums).then((res) => {
        if (res) {
          this.$message.success("批量删除成功");
          this.load();
        } else {
          this.$message.error("批量删除失败");
        }
      });
    },
    reset() {
      this.name = "";
      this.load();
    },
    handleSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = pageSize;
      this.load();
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum);
      this.pageNum = pageNum;
      this.load();
    },
    handleFileUpSuccess(res) {
        console.log(res);
    }   
  },
};
</script>

<style></style>
